@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap");
.header {
  height: 9vh;
  position: relative;
  padding: 1.5% 0 0 5%;
  color: white;
  background-image: linear-gradient(280deg, #6787FE 0%, #0F1B63 100%);
  display: flex;
}
.header .header-name {
  flex: 1;
  font-family: "Poppins", sans-serif;
}
.header .navBar {
  padding-top: 0.5%;
  width: 100%;
  flex: 4.5;
}
.header .navBar ul li {
  font-family: "Rubik", sans-serif;
  font-size: 1.3rem;
  display: inline;
  margin-left: 5%;
}
.header .navBar ul .dashboard {
  margin-left: 10%;
  border: 1px solid white;
  border-radius: 5rem;
  padding: 1%;
}/*# sourceMappingURL=homeNavbar.css.map */